/* Transition */
body, html {
  margin: 0;
  padding: 0;
  width: 100%;
  overflow-x: hidden; /* Prevent horizontal scroll */
}

.card1, .card2, .card3 {
  opacity: 0;
  transform: translateY(40px);
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
}

.card1.animate, .card2.animate, .card3.animate {
  opacity: 1;
  transform: translateY(0);
}

.card-container {
  font-family: Arial, sans-serif;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 3vh 0;
  /* gap: 2vw;  */
}

.card1, .card3 {
  color: white;
  text-align: center;
  padding: 20px;
  padding-bottom: 10px;
  height: 302px;
  width: 345px;
  /* padding: 2vw;
  padding-bottom: 10px;
  height: 302px;
  width: 25vw; */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.842), 0 6px 20px rgba(0, 0, 0, 0.1);
  border-radius: 10px;
  margin-top: 2vh;
  background: linear-gradient(301deg, rgba(231,116,35,1) 0%, rgba(255,184,51,1) 100%);
}

.card2 {
  background: linear-gradient(201deg, rgba(231,116,35,1) 0%, rgba(255,184,51,1) 100%);
  text-align: center;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.842), 0 6px 20px rgba(0, 0, 0, 0.1);
  color: #fff;
  padding: 20px;
  padding-bottom: 30px;
  height: 312px;
  width: 345px;
  /* height: 47vh;
  width: 25vw; */
  position: relative;
  z-index: 5;
  border-radius: 10px;
}

.card2 p {
  font-family: 'Nunito Sans', sans-serif;
  padding-bottom: 33px;
  color: white;
}

.card-container h3 {
  color: white;
  font-family: "Roboto", sans-serif;
  font-weight: 500;
  padding-top: 15px;
  /* font-size: 2vw; */
  font-size: 26px;
  margin-bottom: 50px;
  text-transform: uppercase;
}

.card-container p {
  color: white;
  font-family: "Roboto", sans-serif;
  font-weight: 400;
  font-size: 1.2vw;
  line-height: 21px;
  padding: 0 1vw;
}

/* Media query for mobile devices */
@media (max-width: 768px) {
  .card-container {
    flex-direction: column;
    align-items: center;
    gap: 1vh;
  }

  .card1, .card2, .card3 {
    width: 90%;
    height: auto; /* Let height adjust based on content */
    margin: 1px 0; /* Add some margin between cards */
  }

  .card-container h3 {
    font-size: 4vw; /* Adjust font size for mobile */
    margin-bottom: 2vh;
  }

  .card-container p {
    font-size: 3vw; /* Adjust font size for mobile */
    padding: 0 5vw;
    margin-bottom: 1vh;
    line-height: 14px;
  }
}
