.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 40px;
  box-shadow: 0 1px 3px -2px black;
  color: #000;
  /* background-color: #fff; */
  background-color: rgb(255, 245, 225);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  margin-bottom: 10px;
}

.nav-logo {
  display: flex;
  align-items: center;
  gap: 10px;
  padding-left: 20px;
}

.nav-logo img {
  position: relative;
  left: 100px;
  width: 150px;
  height: auto;
}

.nav-menu-container {
  display: flex;
  flex: 1;
  justify-content: center;
  position: relative;
}

.nav-menu {
  display: flex;
  justify-content: center;
  align-items: center;
  list-style: none;
  gap: 50px;
  font-size: 20px;
  font-weight: 500;
  margin-right: 150px;
  margin-left: 100px;
  
}

.nav-menu li {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 3px;
  cursor: pointer;
  position: relative;
  transition: all 0.3s ease;
}

.nav-menu li:hover,
.nav-menu li.active {
  /* color: #ffa500; */
  color: #FF9800;
}

.nav-menu li::after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 3px;
  bottom: -3px;
  left: 0;
  background-color: #ffa500;
  transform-origin: bottom right;
  transition: transform 0.9s ease-out;
}

.nav-menu li:hover::after,
.nav-menu li.active::after {
  transform: scaleX(1);
  transform-origin: bottom left;
}

.nav-menu .active {
  color: #ffa500;
  font-weight: bold;
}

.nav-menu-container .social-icons {
  display: flex;
  align-items: center;

}

.nav-menu-container .social-icons a {
  color: black;
  font-size: 30px;
  margin: 0 10px;
  transition: transform 0.3s;
}

.nav-menu-container .social-icons a:hover {
  color: #ffa500;
  transform: scale(1.2);
}

.nav-btn {
  display: none;
  padding: 5px;
  cursor: pointer;
  background: transparent;
  border: none;
  color: var(--textColor);
  font-size: 1.8rem;
}

@media (max-width: 1024px) {

  .nav-logo img {
    position: relative;
    width: 120px;
    left: -20px;
  }

  .nav-btn {
    display: block;
  }

  .nav-menu-container {
    position: fixed;
    top: -100vh;
    left: 0;
    height: 80vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    /* background-color: aqua; */
    background-color: #ffd88f;
    transition: 0.4s ease-in-out;
    z-index: 10;
  }

  .nav-menu {
    flex-direction: column;
    align-items: center;
    gap: 20px;
    padding-left: 0; /* Center menu items */
    margin-left: 150px;
    margin-bottom: 50px;
  }

  .responsive_nav {
    transform: translateY(100vh);
  }

  .nav-close-btn {
    position: absolute;
    top: 2rem;
    right: 2rem;
    font-size: 2rem;
  }
}

@media (max-width: 480px) {
  .nav-logo img {
    width: 100px;
  }

  .nav-menu {
    gap: 15px;
    font-size: 16px;
  }

  .nav-menu-container .social-icons a {
    font-size: 25px;
  }
}
