.services {
  padding: 20px;
  margin: 10px auto;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 90%;
}

.services-header {
  text-align: center;
  margin: 5px 30px;
  margin-bottom: 35px;
  font-family: "Playfair Display";

}

.services-header h1 {
  /* color: #ff8c00; */
  color: #FF9800;
  font-size: 2.8rem;
  font-weight: 800;
  margin-bottom: 25px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

.services-header p {
  font-size: 1.2rem;
  color: #333;
}

.services-content {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  justify-content: center;
}

.service-card, .service-card2 {
  flex: 1;
  min-width: 200px;
  max-width: 465px;
  height: 400px;
  background-color: #60563d;
  border-radius: 10px;
  color: white;
  padding: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  position: relative;
  overflow: hidden;
  transition: opacity 0.6s ease-out, transform 0.6s ease-out;
  cursor: pointer;
}

.service-card::before, .service-card2::before {
  content: "";
  background-size: cover;
  background-position: center;
  opacity: 0.3;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}

.service-card::before {
  background-image: url('../assets/service1.png');
}

.service-card2::before {
  background-image: url('../assets/service2.png');
}

.service-card.animate, .service-card2.animate {
  opacity: 1;
  transform: translateY(0);
}

.card-content {
  position: relative;
  z-index: 2;
}

.service-card h2, .service-card2 h2 {
  margin-top: 20px;
  font-size: 2rem;
  margin-bottom: 10px;
  padding: 20px;
  font-family: "Playfair Display";

}

.service-card p, .service-card2 p {
  margin-top: 20px;
  padding: 20px;
  font-size: 1.1rem;
  margin-bottom: 20px;
  color: white;
}

.card-content button {
  margin-top: 30px;
  display: inline-block;
  background-color: transparent;
  color: #ffa500;
  text-decoration: none;
  font-weight: bold;
  transition: all 0.3s ease;
}

.card-content button:hover {
  transform: translateY(-5px);
  color: #e39400;
}

.service-modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background: linear-gradient(135deg, #FFB74D, #FF9800);
  /* background-color: #fff; */
  padding: 20px;
  border-radius: 10px;
  max-width: 90%;
  max-height: 90%;
  overflow-y: auto;
  position: relative;
  /* color: white; */
}
.modal-content h2,h3,h4{
  font-family: "Playfair Display";


}
.modal-content{
  font-size: 1rem;
  line-height: 1.6;
}
.close-button {
  position: absolute;
  top: 10px;
  right: 20px;
  font-size: 2rem;
  color: #333;
  cursor: pointer;
}


@media only screen and (max-width: 768px) {
  .services-content {
    flex-direction: column;
    align-items: center;
  }

  .service-card, .service-card2 {
    width: 90%;
  }

  .services-header h1 {
    font-size: 2rem;
  }

  .service-card h2, .service-card2 h2 {
    font-size: 1.3rem;
  }

  .service-card p, .service-card2 p {
    font-size: 0.9rem;
    margin-top: 50px;
  }

  .modal-content {
    width: 100%;
    height: 100%;
    border-radius: 0;
  }
}
