.who-is-this-coaching-for {
    background-color: #fff;
    background-color: rgb(255, 245, 225);
    padding: 60px 20px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    margin: 40px 0;
    text-align: center;
  }
  
  .section-title {
    font-size: 2.5rem;
    color: #FF9800; /* Matching the orange color from your existing design */
    margin-bottom: 40px;
    font-family: "Playfair Display";
    text-align: center;
    font-size: 2.5rem;
    font-weight: 800;
    /* margin-bottom: 5px; */
    text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
  }
  
  .audience-groups {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px; /* Increase spacing between the boxes */
  }
  
  .audience-group {
    background: linear-gradient(135deg, #FFB74D, #FF9800); /* Gradient for the orange boxes */
    color: white;
    padding: 30px;
    border-radius: 15px;
    width: calc(50% - 40px); /* Each box takes up 50% of the width, minus the gap */
    max-width: 350px; /* Optional: set a max width to control box size */
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
  }
  
  .audience-group:hover {
    transform: translateY(-10px);
  }
  
  .group-title {
    font-size: 1.5rem;
    margin-bottom: 15px;
    font-weight: bold;
    font-family: "Playfair Display";
  }
  
  .group-description {
    font-size: 1.1rem;
    line-height: 1.6;
  }
  
  /* Media query for mobile devices */
@media (max-width: 768px) {
    .who-is-this-coaching-for {
      padding: 40px 15px;
      margin: 30px 0;
    }
  
    .section-title {
      font-size: 2rem;
      margin-bottom: 30px;
    }
  
    .audience-groups {
      flex-direction: column;
      align-items: center;
      gap: 10px;
    }
  
    .audience-group {
      width: 90%; /* Boxes take up 90% of the screen width */
      max-width: 100%; /* Remove max-width restriction on mobile */
      padding: 20px;
    }
  
    .group-title {
      font-size: 1.4rem;
      margin-bottom: 10px;
    }
  
    .group-description {
      font-size: 1rem;
      line-height: 1.4;
    }
  }
  