.hero {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #333;
  padding: 0 20px;
  margin-top: 60px;
  margin-bottom: 60px;
}

.hero-left {
  flex: 1;
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 20px;
  padding-left: 180px;
  color: black;
  text-align: left;
}

.hero-left h2 {
  /* font-family: "Roboto", sans-serif; */
  /* font-family: "Playfair Display"; */
  font-family: "Augustine";
  font-weight: 800;
  font-size: 70px;
  /* color: #ffa500; */
  color: #FF9800;
  text-shadow: 1px 1px #040302;
  margin: 0;
  white-space: nowrap;
}

.hero-left h3 {
  /* font-family: "Roboto", sans-serif; */
  font-family: "Augustine";
  font-weight: 300;
  font-size: 40px;
  /* color: #000; */
  color: rgb(82, 82, 82);
  margin: 0;
  white-space: nowrap;
}

.text1 {
  letter-spacing: 1px;
  margin-bottom: 1px;
  position: relative;
  animation: text 1.5s ease-out;
}

@keyframes text {
  0% {
    color: black;
    margin-bottom: -40px;
  }
  30% {
    letter-spacing: 5px;
    margin-bottom: -40px;
  }
  85% {
    letter-spacing: 2px;
    margin-bottom: -40px;
  }
}

.hero-left p {
  color: #000;
  font-size: 20px;
  font-family: "Nunito Sans", sans-serif;
  /* font-family: "Antique Serif"; */
  font-weight: 100;
  font-style: normal;
  line-height: 30px;
}

.hero-btn {
  font-size: 20px;
  width: 200px;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 750;
  font-style: normal;
  line-height: 30px;
  display: inline-block;
  margin: 5px;
  padding: 10px 20px;
  border-radius: 25px;
  color: white;
  position: relative;
  z-index: 2;
  /* background-color: #ffa500; */
  background: linear-gradient(135deg, #FFB74D, #FF9800);
  /* background-color: #f89822; */
  font-family: "Antique Serif";
  border: 1px solid black;
  text-decoration: none;
  transition: transform 0.3s, background-color 0.3s, color 0.3s, box-shadow 0.3s;
}

.hero-btn:hover {
  color: white;
  background-color: #e09203;
  transform: translateY(-5px);
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.hero-right {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero-right img {
  width: auto;
  height: 500px; /* Adjust the height as needed */
  object-fit: cover;
  border-radius: 20px; /* Ensure image has the same border radius as before */
}

@media (max-width: 768px) {
  .hero {
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 0;
    margin: 0;
  }

  .hero-left {
    padding: 14px 19px;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 12px;
  }

  .hero-left h2 {
    font-size: 46px;
  }

  .hero-left h3 {
    font-size: 22px;
    /* margin-bottom: 14px; */
    /* margin-top: 9px; */
  }

  .hero-left p {
    font-size: 12px;
    font-weight: 400;
    line-height: 16px;
    width: 55%;
    /* margin-bottom: 27px; */
    /* margin-top: 18px; */
  }

  .hero-btn {
    padding: 0 4px;
    font-size: 16px;
    border-radius: 50px;
  }

  .hero-right img {
    width: 225px;
    height: 225px;
    /* margin-top: 14px; */
    border-radius: 50%;
  }
}
