.about {
  padding: 20px;
  margin: 10px auto;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  /* background: linear-gradient(301deg, rgba(231,116,35,1) 0%, rgba(255,184,51,1) 100%); */
  max-width: 90%;
}

.about-header {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px;
}

.about-header h2 {
  /* color: #ffa500;
  font-size: 2.5rem;
  font-weight: 700;
  text-align: left; */
  font-family: "Playfair Display";
  color: #FF9800;
  text-align: center;
  font-size: 2.8rem;
  font-weight: 800;
  margin-bottom: 5px;
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.2);
}

.about-content {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin-bottom: 20px;
  font-family: "Nunito Sans", sans-serif;
}

.about-image {
  flex: 1;
  margin: 20px 20px 20px 0;
}

.about-image img {
  width: 100%;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.about-text {
  
  flex: 2;
  padding-left: 20px;
  margin-top: 20px;
}

.about-text p {
  font-size: 1rem;
  line-height: 1.6;
  color: #333;
  text-align: justify;
  padding-right: 10px;
}

.quote {
  margin-top: 30px;
  padding: 20px;
  background-color: #f7f7f7;
  border-left: 5px solid #FF9800;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.quote p {
  font-size: 1.2rem;
  font-style: italic;
  color: #333;
  margin: 0;
  text-align: center;
}

@media only screen and (max-width: 768px) {
  .about {
    padding: 10px;
  }

  .about-content {
    flex-direction: column;
  }

  .about-image {
    margin: 0 0 20px 0;
  }

  .about-text {
    padding-left: 0;
    text-align: center;
  }

  .about-header h2 {
    font-size: 2rem;
  }

  .about-text p {
    font-size: 1rem;
  }

  .quote p {
    font-size: 1rem;
  }
  .about-image img {
    width: 100%;
    height: auto;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
}
